<template>
  <div style="display: flex;justify-content: center">
    <el-card class="box-card" style="width: 450px;margin-top: 200px">
      <div slot="header" class="clearfix">
        <span>登录</span>
      </div>
      <div>
        <el-row>
          <el-col :span="6" style="display: flex;align-items: center;height: 40px">
            <div>用户名：</div>
          </el-col>
          <el-col :span="18">
            <div>
              <el-input v-model="user.username" placeholder="请输入用户名"></el-input>
            </div>
          </el-col>
        </el-row>
        <el-row style="margin-top: 10px">
          <el-col :span="6" style="display: flex;align-items: center;height: 40px">
            <div>用户密码：</div>
          </el-col>
          <el-col :span="18">
            <div>
              <el-input @keydown.enter.native="doLogin" v-model="user.password" type="password" show-password placeholder="请输入用户密码"></el-input>
            </div>
          </el-col>
        </el-row>
        <el-row style="margin-top: 10px">
          <el-col :span="24">
            <div style="display: flex;justify-content: center">
              <el-button type="primary" style="width: 80%" @click="doLogin">登录</el-button>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-card>
  </div>
</template>

<script>

//导入请求方法
// import {postKeyValueRequest} from "@/utils/api";

export default {
  name: "Login",
  data() {
    return {
      user: {
        username: 'admin',
        password: '123'
      }
    }
  },
  methods:{
    doLogin() {
      this.postKeyValueRequest('/login',this.user).then(resp=>{
        if (resp) {
          //说明请求成功
          //push 是入栈
          // this.$router.push()
          //保存用户信息 Cookie、sessionStorage（浏览器关闭重启之后，数据就会丢失）、localStorage（存在这个中的数据序号要自己手动移除或者代码移除）
          //将用户信息转为一个字符串之后，存入到 sessionStorage
          window.sessionStorage.setItem("hr", JSON.stringify(resp.data));
          //获取当前浏览器地址栏名为 redirect 的参数值
          let redirect = this.$router.currentRoute.query.redirect;
          console.log(redirect);
          //页面跳转之后，无法回到上一个页面，登录成功后，跳转到项目首页
          this.$router.replace(redirect ? redirect : '/home');
        }
      })
    }
  }
}
</script>

